.main-header {
  display: flex;
  position: relative;
  justify-content: space-between;
  min-height: 63px;
  padding: 0 20px 0 20px;
  background-color: #ffffff;
  align-items: center;
}
@media (min-width: 768px) {
  .main-header {
    display: flex;
    padding: 19px 34px 25px 30px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding: 55px 110px;
  }
}

@media (min-width: 1440px) {
  .main-header__hero-bg::before {
    content: "";
    position: absolute;
    display: flex;
    right: 0;
    transform: none;
    left: revert;
    width: 720px;
    height: 694px;
    top: 0;
    background-image: url("../../img/hero-block/index-background-desktop 1.png");
    background-repeat: no-repeat;
    z-index: 1;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-header__hero-bg::before {
    content: "";
    background-image: url("../../img/hero-block/index-background-desktop 1@2x.png");
  }
}
@media (min-width: 1440px) {
  .main-header__hero-bg::after {
    content: "";
    position: absolute;
    display: flex;
    right: 0;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: 2;
    width: 720px;
    height: 694px;
    top: 0;
  }
}

.main-header__no-background::before {
  display: none;
}
.main-header__no-background::after {
  border-bottom: 1px solid #d9d9d9;
  position: absolute;
  display: block;
  flex-grow: 1;
  content: "";
  width: 100%;
  height: 1px;
  left: 0;
  bottom: -3px;
}
@media (min-width: 768px) {
  .main-header__no-background::after {
    display: none;
  }
}

