.hero-block {
  position: relative;
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
.hero-block::before {
  content: "";
  position: absolute;
  height: 302px;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../../img/hero-block/index-background-mobile 2.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: -2;
}
.hero-block::after {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 304px;
  z-index: -1;
  background-color: rgba(104, 183, 56, 0.85);
  margin-left: calc((50vw - 50%) * -1);
  margin-right: calc((50vw - 50%) * -1);
}
@media (min-width: 768px) {
  .hero-block {
    margin: 0;
  }
  .hero-block::before {
    display: none;
  }
  .hero-block::after {
