/* Mark */
.control-mark {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  outline: solid 1px #cdcdcd;
  cursor: pointer;
}
.control-mark:hover {
  outline-color: #bababa;
}
@media (min-width: 768px) {
  .control-mark {
    width: 40px;
    height: 40px;
  }
}
@media (min-width: 1440px) {
  .control-mark {
    left: -7px;
  }
}

input[type=radio] ~ .control-mark {
  border-radius: 50%;
}

input[type=radio]:checked ~ .control-mark::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
  width: 15px;
  height: 15px;
  background-color: #68b738;
  border-radius: 50%;
}
@media (min-width: 768px) {
  input[type=radio]:checked ~ .control-mark::before {
    width: 18px;
    height: 18px;
  }
}

.control-mark-ckeckbox {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 28px;
  height: 29px;
  outline: solid 1px #cdcdcd;
